import { addLog } from '../utils/applogs';
import _ from 'lodash';

export const loadSensors = () => {
  addLog('loadSensors()');
  return fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor`)
    .then(response => response.json())
    .then(data => {
      console.log('loadSensors(1)', data);
      return data.sensors;
    })
    .catch((error) => {
      console.error('ERROR', error);
    });
}

export const updateSensor = ({ sensorId, isFavourite, snoozeUntil }) => {
  addLog('updateSensor: ', sensorId);
  const patchObject = {};
  if (!_.isUndefined(isFavourite)) {
    patchObject.isFavourite = isFavourite;
  }
  if (!_.isUndefined(snoozeUntil)) {
    patchObject.snoozeUntil = snoozeUntil;
  }
  return fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor/${sensorId}`,
    {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: 'PATCH',
      body: JSON.stringify(patchObject)
    }
  )
    .then(response => response.json())
    .catch((error) => {
      console.error('ERROR', error);
    });
};
