import React from 'react';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import { Map, GroupBy } from 'react-lodash';
import { addLog } from './utils/applogs';
import MenuItem from '@material-ui/core/MenuItem';
import NotificationEventsGroup from './NotificationEventsGroup';
import { loadSensors, updateSensor } from './service/sensors';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import Skeleton from '@material-ui/lab/Skeleton';
import Grid from '@material-ui/core/Grid';

const skeleton = () => {
  return (
    <Grid container spacing={1} alignItems="center">
      <Map collection={[1, 2, 3]} iteratee={idx =>
        <Grid item xs={12}>
          <Skeleton key={idx} variant="rect" width={309} height={80} />
        </Grid>
      } />
    </Grid>
  );
}

export default function NotificationEventsMenuItem({ onClose }) {

  const [openEvents, setOpenEvents] = React.useState(false);

  const handleClickEvents = () => {
    setOpenEvents(false);
    onClose();
  };

  const reloadData = () => {
    addLog('reloadData');
    return fetch(`https://aoq53tdls8.execute-api.us-east-1.amazonaws.com/dev/sensorNotificationEvent`)
      .then(response => response.json())
      .catch((error) => {
        console.error('ERROR', error);
      });
  }

  const handleOpenEvents = () => {
    setOpenEvents(true);
  };

  const { data: notificationEvents = [], isLoading } = useQuery('loadNotificationEvents', reloadData, { refetchInterval: 60000 });

  const queryClient = useQueryClient()
  const { data: sensors } = useQuery('loadSensors', loadSensors);
  const mutation = useMutation(updateSensor, {
    onSuccess: () => {
      queryClient.invalidateQueries('loadSensors')
    },
  });

  return (
    <React.Fragment>
      <MenuItem onClick={handleOpenEvents}>
        Notification Events
      </MenuItem>
      <Dialog onClose={handleClickEvents} aria-labelledby="simple-dialog-title" open={openEvents} fullWidth={true} maxWidth={'xs'}>
        <DialogTitle id="simple-dialog-title">Notification Events</DialogTitle>
        <List>
          {isLoading && skeleton()}
          {!isLoading &&
            <GroupBy collection={notificationEvents} iteratee={(entry) => entry.sensorId}>
              {(value) =>
                <Map collection={Object.keys(value)} iteratee={sensorId =>
                  <NotificationEventsGroup
                    eventsList={value[sensorId]}
                    handleClickEvents={handleClickEvents}
                    key={sensorId}
                    sensorMutation={mutation}
                    sensor={sensors && sensors.filter(item => `${item.sensorId}` === sensorId)[0]}
                  />
                } />
              }
            </GroupBy>
          }
        </List>
      </Dialog>
    </React.Fragment>
  )
}