import React from 'react';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';

import { makeStyles } from '@material-ui/core/styles';
import NotificationEventsMenuItem from './NotificationEventsMenuItem';
import ApplicationLogsMenuItem from './ApplicationLogsMenuItem';
import { addLog } from './utils/applogs';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  dialog2: {
    minWidth: "400px"
  }
}));

export default function MenuBar() {

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
    addLog('handleMenu');
  };

  const handleClose = () => {
    setAnchorEl(null);
    addLog('handleClose');
  };

  return (
    <React.Fragment>
      <AppBar position="fixed" className={classes.root}>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            open={open}
            onClose={handleClose}
          >
            <div>
              <NotificationEventsMenuItem onClose={handleClose} />
            </div>
            <div>
              <ApplicationLogsMenuItem onClose={handleClose} />
            </div>
          </Menu>
        </Toolbar>
      </AppBar>
      <Toolbar />
    </React.Fragment>
  )
}