function getItem() {
    const item = localStorage.getItem('logs');
    if (item === null) {
        return '[]';
    } else {
        return item;
    }
}

export function addLog(label, obj) {
    const item = getItem();
    const logs = JSON.parse(item || []);
    if (obj) {
        logs.push({id: logs.length, message:`${label}: ${obj}`});
    } else {
        logs.push({id: logs.length, message:`${label}`});
    }
    const newLogs = JSON.stringify(logs);
    localStorage.setItem('logs', newLogs);
}

export function logs() {
    const item = getItem();
    return JSON.parse(item);
}

export function clear() {
    localStorage.setItem('logs', '[]');
}