import React from 'react';
import SensorItem from './SensorItem'
import { makeStyles } from '@material-ui/core/styles';
import {
  SwipeableList,
  SwipeableListItem
} from '@sandstreamdev/react-swipeable-list';
import '@sandstreamdev/react-swipeable-list/dist/styles.css';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Grid from '@material-ui/core/Grid';
import Skeleton from '@material-ui/lab/Skeleton';
import { Map, Range, Filter } from 'react-lodash';
import { useMutation, useQueryClient, useQuery } from 'react-query';
import { updateSensor, loadSensors } from './service/sensors';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 440,
    backgroundColor: theme.palette.background.paper,
  },
}));

const skeleton = () => {
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Grid container spacing={1}>
          <Range start={10}>
            {array =>
              <Map collection={array} iteratee={id =>
                <Grid item xs={12} key={id}>
                  <Skeleton variant="rect" width={'100%'} height={56} />
                </Grid>
              } />
            }
          </Range>
        </Grid>
      </Grid>
    </Grid>
  );
}

const contents = (sensorData, classes, mutation, favouriteSensors) => {
  const items = Object.keys(sensorData.items)
    .sort((a, b) => sensorData.items[a][0].name.localeCompare(sensorData.items[b][0].name));

  const swipeRightOptions = sensorId => ({
    content: <FavoriteIcon />,
    action: () => mutation.mutate({ sensorId, isFavourite: true })
  });

  return (
    <SwipeableList className={classes.root}>
      <Filter collection={items} predicate={item => !favouriteSensors.includes(item) }>
        {sensors =>
          <Map collection={sensors} iteratee={item =>
            <SwipeableListItem key={item} swipeRight={swipeRightOptions(item)} >
              <SensorItem sensors={sensorData.items[item]} />
            </SwipeableListItem>
          } />
        }
      </Filter>
    </SwipeableList>
  );
}

export default function SensorList({ sensorData, isLoading }) {
  const classes = useStyles();

  const queryClient = useQueryClient();

  const mutation = useMutation(updateSensor, {
    onSuccess: () => {
      queryClient.invalidateQueries('loadSensors')
    },
  });

  const { data: sensors = [] } = useQuery('loadSensors', loadSensors);
  const [favouriteSensors, setFavouriteSensors] = React.useState([]);

  React.useEffect(() => {
    setFavouriteSensors(
      sensors.filter(sensor => sensor.isFavourite)
        .map(sensor => `${sensor.sensorId}`)
    );
  }, [sensors]);

  if (isLoading) {
    return skeleton();
  } else {
    return contents(sensorData, classes, mutation, favouriteSensors);
  }
}