import React from 'react';
import SensorList from './SensorList';
import SensorsFavourites from './SensorFavourites';
import Container from '@material-ui/core/Container';
import { loadSensorValues } from './service/sensorValues';
import { useQuery } from 'react-query';

export default function SensorsScreen() {
  const {
    data: sensorData = { items: [], lastUpdated: new Date() },
    isLoading
  } = useQuery('loadSensorValues', loadSensorValues, { refetchInterval: 60000 });

  return (
    <Container maxWidth="sm">
      <SensorsFavourites sensorData={sensorData} isLoading={isLoading} />
      <SensorList sensorData={sensorData} isLoading={isLoading} />
    </Container>
  );
}
