import React from 'react';
import Button from '@material-ui/core/Button';
import { blueGrey, pink, teal, indigo, grey } from '@material-ui/core/colors';
import SensorValueListDialog from './SensorValueListDialog';

const tempColor = teal["400"];
const humidityColor = indigo["400"];
const uvColor = blueGrey["400"];
const lumColor = pink["400"];
const defaultColor = grey["400"];

const getColor = (value, type) => {
  switch (type) {
    case 'temp': return tempColor;
    case 'humidity': return humidityColor;
    case 'uv': return uvColor;
    case 'lum': return lumColor;
    default: return defaultColor;
  }
}

const getUnitSign = (type) => {
  switch (type) {
    case "temp": return "C";
    case "humidity": return "%";
    case "lum": return "L";
    case "uv": return "UV";
    default: return "N/A";
  }
}

export default function ValueButton({ sensor }) {

  const [open, setOpen] = React.useState(false);

  const onClose = () => {
    setOpen(false);
  };

  const formatOptions = sensor.type === 'temp' 
    ? {minimumFractionDigits: 1, maximumFractionDigits: 1}
    : {minimumFractionDigits: 0, maximumFractionDigits: 0};

  return (
    <>
      <Button
        variant="contained"
        key={sensor.type}
        style={{ backgroundColor: getColor(sensor.data.slice().reverse()[0][1], sensor.type), maxWidth: 64 }}
        onClick={() => setOpen(true)}
      >{sensor.data.slice().reverse()[0][1].toLocaleString(undefined, formatOptions) + getUnitSign(sensor.type)}
      </Button>
      <SensorValueListDialog sensor={sensor} open={open} onClose={onClose} />
    </>
  )
}