import React from 'react';

import MenuItem from '@material-ui/core/MenuItem';

import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import { Map } from 'react-lodash';

import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { logs, clear } from './utils/applogs';
import Clear from '@material-ui/icons/Clear';

export default function ApplicationLogsMenuItem({ onClose }) {

  const [openLogs, setOpenLogs] = React.useState(false);

  const handleClickLogs = () => {
    setOpenLogs(false);
    onClose();
  };

  const handleOpenLogs = () => {
    setOpenLogs(true);
  };


  return (
    <React.Fragment>
      <MenuItem onClick={handleOpenLogs}>
        Application Logs
      </MenuItem>
      <Dialog onClose={handleClickLogs} aria-labelledby="simple-dialog-title" open={openLogs} fullWidth={true} maxWidth={'xs'}>
        <DialogTitle id="simple-dialog-title">
          Application Logs
          <Clear onClick={clear}></Clear>
        </DialogTitle>
        <List>
          <Map collection={logs()} iteratee={logrow =>
            <ListItem key={logrow.id}>
              <ListItemText primary={logrow.message || logrow} />
            </ListItem>
          } />
        </List>
      </Dialog>
    </React.Fragment>
  )
}